<template>
  <v-dialog v-model="dialogo" scrollable max-width="400">
    <template v-slot:activator="{ on, attrs }">
      <v-btn v-on="on" v-bind="attrs" color="primary">
        <v-icon left>mdi-filter-variant</v-icon> Filtros
      </v-btn>
    </template>
    <v-card class="mx-auto">
      <v-card-title class="grey lighten-2">
        <v-btn icon color="primary" @click="dialogo = false" x-large>
          <v-icon>mdi-chevron-left</v-icon>
        </v-btn>
        <v-spacer />
        <v-btn icon color="primary" @click="cleanAll()" x-large><v-icon>mdi-eraser</v-icon></v-btn>
      </v-card-title>
      <v-card-text class="pt-2">
        <v-autocomplete
          v-model="estado"
          label="Estados"
          outlined
          item-text="nombre"
          :items="estados"
          :disabled="isEstados"
          return-object
          :loading="isCargandoEstados"
          clearable
        />
        <v-autocomplete
          v-model="municipio"
          label="Municipios"
          outlined
          item-text="nombre"
          :items="municipios"
          :disabled="isMunicipios"
          :loading="isCargandoMunuicipios"
          clearable
          return-object
        />
        <v-autocomplete
          v-model="localidad"
          label="Localidades"
          outlined
          item-text="nombre"
          :items="localidades"
          :disabled="isLocalidades"
          :loading="isCargandoLocalidades"
          clearable
          return-object
        />
      </v-card-text>
      <v-card-actions>
        <v-btn block color="primary" @click="buscarInmueblesTerritorio()">Ver Inmuebles</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  data: () => ({
    dialogo: false,
    localidad: null,
  }),
  computed: {
    cliente() { return this.$store.getters["MOD_clientes/getCliente"].id; },
    estados() { return this.$store.getters["MOD_ubicaciones/getEstados"]; },
    estado: {
      get() { return this.$store.getters["MOD_ubicaciones/getEstado"]; },
      async set(nuevoValor) {
        if (nuevoValor) {
          await this.$store.dispatch("MOD_ubicaciones/obtenerEstado", nuevoValor);
          await this.obtenerMunicipiosEstado();
        } else {
          this.$store.commit("MOD_ubicaciones/cleanMunicipios")
          this.$store.commit("MOD_ubicaciones/cleanLocalidades")
        }
      },
    },
    isEstados() { return this.estados.length ? false : true; },
    isCargandoEstados() { return this.$store.getters["MOD_ubicaciones/getCargandoEstados"]; },
    municipios() { return this.$store.getters["MOD_ubicaciones/getMunicipios"]; },
    municipio: {
      get() { return this.$store.getters["MOD_ubicaciones/getMunicipio"]; },
      async set(nuevoValor) {
        if(nuevoValor){
          await this.$store.dispatch("MOD_ubicaciones/obtenerMunicipio", nuevoValor);
          await this.obtenerLocalidadesMunicipio()
        } else { this.$store.commit("MOD_ubicaciones/cleanLocalidades") }
      },
    },
    isMunicipios() { return this.municipios.length ? false : true; },
    isCargandoMunuicipios() { return this.$store.getters["MOD_ubicaciones/getCargandoMunicipios"]; },
    localidades() { return this.$store.getters["MOD_ubicaciones/getLocalidades"]; },
    isLocalidades() { return this.localidades.length ? false : true; },
    isCargandoLocalidades() { return this.$store.getters["MOD_ubicaciones/getCargandoLocalidades"]; },
  },
  created() {
    this.inicializarEstados();
  },
  methods: {
    async inicializarEstados() {
      await this.$store.dispatch("MOD_ubicaciones/obtenerEstados");
    },
    async obtenerMunicipiosEstado() {
      const {cliente, estado} = this
      await this.$store.dispatch(
        "MOD_ubicaciones/obtenerMunicipiosDeInmueblesExistentes", { cliente, estado }
      );
    },
    async obtenerLocalidadesMunicipio() {
      const { cliente, estado, municipio } = this
      await this.$store.dispatch(
        "MOD_ubicaciones/obtenerLocalidadesDeInmueblesExistentes", { cliente, estado, municipio }
      );
    },
    async buscarInmueblesTerritorio() {
      const { cliente, estado, municipio, localidad } = this
      const { mensaje, estadoHttp, error } = await this.$store.dispatch(
        "MOD_inmuebles/listarPorClientesTerritorio",{ cliente: [cliente], estado, municipio, localidad }
      );
      if (error) {
        this.$store.commit("alerta/addAlertas", { "mensaje": mensaje, "estado": estadoHttp, "error": error });
      }
    },
    cleanAll(){
      this.$store.commit("MOD_ubicaciones/cleanEstado")
      this.$store.commit("MOD_ubicaciones/cleanMunicipio")
      this.$store.commit("MOD_ubicaciones/cleanLocalidad")
    }
  },
};
</script>