<template>
  <v-row justify="center">
    <v-col cols="8">
      <listar-trabajos :isAsignarBtn="true" :headers="headers"/>
    </v-col>
  </v-row>
</template>

<script>
import ListarTrabajos from "../../components/trabajo/TablaTrabajos.vue";
export default {
  data: () => ({
    headers: [
      { value: "concept.project", text: "Proyecto", sortable: false },
      { value: "concept.name", text: "Concepto", sortable: false },
      { value: "status", text: "Status" },
      { value: "property_office", text: "Inmueble", sortable: false },
      { value: "property_office.client", text: "Cliente", sortable: false },
      { value: "assigned_user", text: "Asignado", sortable: false },
      { value: "area_user", text: "Responsable", sortable: false },
      { value: "created_date", text: "Creado", sortable: false },
      { value: "modified_date", text: "Modificado", sortable: true },
    ]
  }),
  components: {
    "listar-trabajos": ListarTrabajos,
  },
  computed:{
    cliente() { return this.$store.getters["MOD_clientes/getCliente"] }
  },
  created(){
    this.inicializarTrabajosCliente()
  },
  methods:{
    async inicializarTrabajosCliente(){
      await this.$store.dispatch("MOD_trabajos/obtenerTrabajosCliente", {cliente: this.cliente.id})
    }
  }
};
</script>
