<template>
  <v-card class="mx-auto" outlined>
    <v-card-title class="grey lighten-2">
      Inmuebles
    </v-card-title>
    <v-card-subtitle class="grey lighten-2">
      {{ inmuebles.length == 1 ? `${inmuebles.length} Inmueble` : `${inmuebles.length} Inmuebles` }}
    </v-card-subtitle>
    <v-card-text>
      <v-responsive class="overflow-y-auto" max-height="500">
        <v-data-table
          class="elevation-5"
          v-model="seleccionados"
          :headers="headers"
          :items="inmuebles"
          item-key="clave"
          :loading="isCargando"
          no-data-text="No hay inmuebles disponibles"
          loading-text="Cargando... por favor espere"
          :items-per-page="10"
          :footer-props="{
            itemsPerPageText: 'Filas por página',
            itemsPerPageAllText: 'Todos',
            pageText: 'p - Tp de Fp',
          }"
          :show-select="isMostrarSeleccion"
        >
          <template v-slot:top >
            <v-toolbar flat>
              <v-spacer />
              <v-menu left offset-y v-if="isMostrarMenuInmuebles">
                <template v-slot:activator="{ attrs, on }">
                  <v-btn class="ma-5 info" v-bind="attrs" v-on="on">
                    Añadir inmueble <v-icon right>{{attrs["aria-expanded"] === "true" ? "mdi-menu-up" : "mdi-menu-down"}}</v-icon>
                  </v-btn>
                </template>
                <v-list>
                  <crear-inmueble/>
                  <v-list-item link @click="$router.push({ name: 'cargar-archivo', params: cliente.name })">
                    <v-list-item-title>
                      Cargar archivo
                    </v-list-item-title>
                  </v-list-item>
                </v-list>
              </v-menu>
              <filtrado-inmueble v-if="isBtnFiltrar"/>
            </v-toolbar>
          </template>
        </v-data-table>
      </v-responsive>
    </v-card-text>
  </v-card>
</template>

<script>
import FormularioCrearInmueble from  "../../components/Inmueble/FormularioCrearInmueblesManual.vue"
import Filtrado from "../../components/Inmueble/Filtrado.vue"
export default {
  props: {
    isMostrarSeleccion: { type: Boolean, default: false },
    isMostrarMenuInmuebles: { type: Boolean, default: true },
    isBtnFiltrar: { type: Boolean, default: true }
  },
  data: () => ({
    headers: [
      { value: "cliente", text: "Cliente", sortable: false },
      { value: "clave", text: "Clave" },
      { value: "nombre", text: "Inmueble" },
      { value: "estado", text: "Estado" },
      { value: "municipio", text: "Municipio" },
      { value: "localidad", text: "Localidad" },
      { value: "direccion", text: "Direccion", sortable: false },
    ]
  }),
  components:{
    "crear-inmueble": FormularioCrearInmueble,
    "filtrado-inmueble": Filtrado
  },
  computed: {
    inmuebles() { return this.$store.getters["MOD_inmuebles/getInmuebles"]; },
    isCargando() { return this.$store.getters["MOD_inmuebles/getCargando"]; },
    seleccionados: {
      get() { return this.$store.getters["MOD_inmuebles/getInmueblesSeleccionados"] },
      set(nuevoValor) {
        this.$store.commit("MOD_inmuebles/setInmueblesSeleccionados", nuevoValor);
      },
    },
    cliente() { return this.$store.getters["MOD_clientes/getCliente"] }
  },
  created() {
    this.inicializarInmuebles()
  },
  methods:{
    async inicializarInmuebles(){
      const { mensaje, estado, error } = await this.$store.dispatch("MOD_inmuebles/listar", this.cliente.id)
      this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
    },
  }
};
</script>
