<template>
  <v-card class="mx-auto" outlined elevation="5">
    <v-card-title class="grey lighten-2">
      Trabajos
      <v-spacer></v-spacer>
      <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        label="Buscar"
        single-line
        hide-details
        clearable
        dense
        filled
        outlined
        rounded
        class="white"
      />
    </v-card-title>
    <v-card-subtitle class="grey lighten-2"> {{ trabajos.length + (trabajos.length === 1 ? ' Trabajo' : ' Trabajos') }} </v-card-subtitle>
    <v-card-text>
      <v-responsive class="overflow-y-auto" max-height="500">
        <v-data-table
        v-model="trabajos_seleccionados"
          class="elevation-5"
          :loading="isCargando"
          :headers="headers"
          :items="trabajos"
          :items-per-page="-1"
          loading-text="Cargando... por favor espere"
          :footer-props="{
            itemsPerPageText: 'Filas por página',
            itemsPerPageAllText: 'Todos',
            pageText: ' p - Tp de n',
          }"
          no-data-text="No hay trabajos"
          :search="search"
          :show-select="mostrar_seleccionados"
          :single-select="singleSelect"
        >
        <!-- @click:row="verTrabajo" -->
          <template v-slot:top>
            <v-toolbar flat>
              <v-spacer />
              <v-menu
                v-if="is_peticion"
                offset-y
                left
                :disabled="isTrabajos"
              >
                <template v-slot:activator="{on, attrs}">
                  <v-btn class="ma-5 primary" v-bind="attrs" v-on="on">
                    Solicitar recurso <v-icon right>{{attrs["aria-expanded"] === "true" ? "mdi-menu-up" : "mdi-menu-down"}}</v-icon>
                  </v-btn>
                </template>
                <v-list>
                  <v-list-item
                    link
                    @click="$router.push({ name: 'peticion-recursos' })"
                  >
                    <v-list-item-title>
                      Crear nueva solicitud de recurso
                    </v-list-item-title>
                  </v-list-item>
                  <v-list-item link>
                    <v-list-item-title>
                      Crear +1 solicitud de recurso
                    </v-list-item-title>
                  </v-list-item>
                </v-list>
              </v-menu>
              <v-btn
                v-if="isAsignarBtn"
                @click="$router.push({name: 'asignar-trabajos'})"
                :disabled="isInmuebles"
                class="success ma-1"
              >
                Asignar trabajo
                <v-icon right>mdi-file-account</v-icon>
              </v-btn>
              <filtrado-trabajos v-if="isFiltadoBtn"/>
            </v-toolbar>
          </template>
          <template v-slot:item.modified_date="{ item }">
            <v-chip :color="getColor(item.modified_date)" dark>
              {{ item.modified_date }}
            </v-chip>
          </template>
          <template v-slot:item.property_office="{item}">
            {{ item.property_office ? item.property_office.name : '' }} ({{ item.property_office ? item.property_office.property_key : '' }})
          </template>
        </v-data-table>
      </v-responsive>
    </v-card-text>
  </v-card>
</template>

<script>
import FiltradoTrabajos from '../../components/trabajo/Filtrado.vue'
export default {
  props: {
    isFiltadoBtn: { type: Boolean, default: false },
    isAsignarBtn: { type: Boolean, default: false },
    mostrar_seleccionados: { type: Boolean, default: false },
    is_peticion: { type: Boolean, default: false },
    headers: { type: Array, default: () => {} }
  },
  data: () => ({
    search: "",
    blue: 3,
    singleSelect: false
  }),
  components:{
    'filtrado-trabajos': FiltradoTrabajos
  },
  computed: {
    trabajos() { return this.$store.getters["MOD_trabajos/getTrabajos"] },
    isCargando() { return this.$store.getters["MOD_trabajos/getCargando"] },
    isInmuebles() { return !this.$store.getters["MOD_inmuebles/getInmuebles"].length; },
    isTrabajos() { return this.trabajos.length ? false : true; },
    trabajos_seleccionados: {
      get(){ return this.$store.getters["MOD_trabajos/getTrabajosSeleccionados"] },
      set(nuevoValor){
        const peticiones = []
        for(const trabajos_seleccionado of nuevoValor){
          peticiones.push({ 'trabajo': trabajos_seleccionado.id, 'monto': 0.0, 'datos_bancarios': 2, 'tipo_pago': '', 'beneficiario': '', 'banco': ''})
        }
        this.$store.commit("MOD_pagos/setVerificarPeticiones", peticiones)
        this.$store.commit("MOD_trabajos/setSeleccionados", nuevoValor)
      }
    },
  },
  methods: {
    holgura(modified_date) {
      var date1 = new Date(modified_date);
      var date2 = new Date();
      var datestring =
        date2.getFullYear() +
        "-" +
        (date2.getMonth() + 1) +
        "-" +
        date2.getDate();
      var date3 = new Date(datestring);
      var holgura = (date3.valueOf() - date1.valueOf()) / 86400000;
      if (this.blue < holgura) this.blue = holgura;
      return holgura;
    },
    getColor(modified_date) {
      if (this.holgura(modified_date) >= 5) return "red";
      else if (this.holgura(modified_date) >= 2) return "orange";
      else return "green";
    },
    verTrabajo(value) {
      this.$store.commit("MOD_trabajos/setTrabajo", value);
      this.$router.push({
        name: "De",
      });
    },
  },
};
</script>
